import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import CategoriesMenu from './presenters/categories_menu.view';
import { getCategory } from './categories.action';
import constants from '../../shared/constants';

const showCategory = (Menu) => {
    return class extends Component {
        constructor(props) {
            super(props)
            this.pageIndex = constants.page_Index;
            this.pageSize = constants.page_Size;
        }
        componentDidMount() {
            this.props.getCategory(this.pageIndex, this.pageSize);
        }

        render() {
            return (
                <div className="sub-cate">
                    <div className=" top-nav rsidebar span_1_of_left">
                        <h3 className="cate">CATEGORIES</h3>
                        <ul className="menu">
                            <Menu {...this.props} />
                        </ul>
                    </div>
                </div>
            )
        }
    }
}

function mapStateToProps(state) {
    return {
        menu_items: state.categoryReducer.categories
    };
}
function mapDispatchToProps(dispatch) {
    return bindActionCreators({
        getCategory
    }, dispatch);
}

const Categories = connect(mapStateToProps, mapDispatchToProps)(showCategory(CategoriesMenu));
export default Categories;



