import {
    INITIALIZE_PAGE
} from './list_all_products.action.js';

const initialState = {
    fetching: false,
    fetched: false,
    error: null,
    initialized: false,
    products: [],
    currentIndex: 0,
    pageSize: 0,
    totalPage: 0
};


export default function (state = initialState, action) {
    switch (action.type) {
        case INITIALIZE_PAGE + "_FULFILLED":
            {
                let data = action.payload;
                return {
                    ...state,
                    initialized: true,
                    fetching: false,
                    fetched: true,
                    products: data && data.Items ? data.Items : [],
                    currentIndex: data && data.SelectedPage ? data.SelectedPage : 0,
                    pageSize: data && data.Size ? data.Size : 0,
                    totalPage: data && data.Total ? data.Total : 0
                };
            }
    }
    return state;
}




