import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Page from '../../shared/components/page';
import { initializePage } from '../list_all_products/list_all_products.action';
import constants from '../../shared/constants';
import PaginationView from '../pagination/presenters/pagination.view';
import { showLoading, hideLoading } from 'react-redux-loading-bar';

class Pagination extends Component {
    constructor(props) {
        super(props);
        this.pageSize = constants.page_Size;
        this.paginationPage = this.paginationPage.bind(this);
        this.calculateTotalPage = this.calculateTotalPage.bind(this);
    }

    calculateTotalPage() {
        let totalPage = 0;
        totalPage = parseInt(this.props.totalPage / this.pageSize);
        if (this.props.totalPage % this.pageSize != 0)
            totalPage++;
        return totalPage;
    }

    paginationPage(data) {
        let pageIndex = data.selected;
        this.props.initializePage(pageIndex, this.pageSize,
            this.props.showLoading, this.props.hideLoading);
    }

    render() {

        let totalPage = this.calculateTotalPage();

        return (
            <div>
                <PaginationView paginationPage={this.paginationPage} totalPage={totalPage} />
            </div >
        );

    }
}

function mapStateToProps(state) {
    return {
        totalPage: state.listAllProductsReducer.totalPage
    };
}
function mapDispatchToProps(dispatch) {
    return bindActionCreators({
        initializePage,
        showLoading,
        hideLoading
    }, dispatch);
}
export default connect(mapStateToProps, mapDispatchToProps)(Pagination);




