import React, { Component } from 'react';
import Spinner from '../../../shared/spinner/spinner.container';
const styles = {
    spinner: { position: 'relative', height: '200px' }
}
const ProductImageLoading = (imagesrc) => {
    return (
        <div>
            <div className="img-responsive chain" style={styles.spinner}>
                <Spinner />
            </div>

            {/*status || imagesrc || 1 ?
            <Spinner />
            :  <a href="#">
                <img className="img-responsive chain"
                    src={require(imagesrc)} alt=" " />
            </a>*/}
        </div>
    )
}

export default ProductImageLoading



