import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import renderBody from '../../shared/components/page';
import { initializePage } from '../list_products/list_products.action';
import ProductSlider from '../product_slider/product_slider.container';
import Pagination from '../pagination/pagination.container';
import constants from '../../shared/constants';
import ProductItem from '../../home/product_item/product_item.container';
import { showLoading, hideLoading } from 'react-redux-loading-bar';
import { Link } from 'react-router';

class ListProductPage extends Component {
    constructor(props) {
        super(props);
        this.pageIndex = constants.page_Index;
        this.pageSize = constants.page_Size;
        this.renderListProducts = this.renderListProducts.bind(this);
    }
    componentDidMount() {
        this.props.initializePage(this.pageIndex, this.pageSize,
            this.props.showLoading, this.props.hideLoading).then((data) => {

            });
    }

    renderProductItem(data) {
        return
    }

    renderListProducts(list_products) {
        return list_products.map((product, index) => {
            return (<div className="col-md-4 chain-grid " key={index}>
                <ProductItem {...product} />
            </div >)
        })
    }

    render() {
        return (
            <div className="shoes-grid">
                <div className="wrap-in">
                    <div className="wmuSlider example1 slide-grid">
                        <div className="wmuSliderWrapper">
                            <ProductSlider />
                        </div>
                    </div>
                </div>
                <div className="products">
                    <h5 className="latest-product">LATEST PRODUCTS</h5>
                    <Link className="view-all" to={`/list-products`}> VIEW ALL<span> </span></Link>
                </div>
                <div className="product-left">
                    {this.props.list_products ? this.renderListProducts(this.props.list_products) : <div ></div >}
                    <div className="clearfix"> </div>
                </div >
                <Pagination />
                <div className="clearfix"> </div>
            </div >
        );
    }
}

function mapStateToProps(state) {
    return {
        list_products: state.listProductsReducer.products
    };
}
function mapDispatchToProps(dispatch) {
    return bindActionCreators({
        initializePage,
        showLoading,
        hideLoading
    }, dispatch);
}
export default connect(mapStateToProps, mapDispatchToProps)(renderBody(ListProductPage));


