import React, { Component } from 'react';

const CategoriesMenu = ({ menu_items }) => {
    return (
        <ul className="cute">
            {
                menu_items.map((item, index) => <li className="subitem1" key={index}><a href="product.html">{item.Name} </a></li>)
            }
        </ul>
    )
}

export default CategoriesMenu



