import { combineReducers } from 'redux';
import { reducer as formReducer } from 'redux-form';
import { loadingBarReducer } from 'react-redux-loading-bar';
//Home
import listProductsReducer from '../modules/home/list_products/list_products.reducer';
import productItemReducer from '../modules/home/product_item/product_item.reducer';
//Common
import spinnerReducer from '../modules/shared/spinner/spinner.reducer';
//Categories
import categoryReducer from '../modules/shared/categories/categories.reducer';
//Product
import listAllProductsReducer from '../modules/products/list_all_products/list_all_products.reducer';

const rootReducer = combineReducers({
    state: (state = {}) => state,
    form: formReducer,
    listProductsReducer: listProductsReducer,
    productItemReducer: productItemReducer,
    loadingBar: loadingBarReducer,
    spinnerReducer: spinnerReducer,
    categoryReducer: categoryReducer,
    listAllProductsReducer: listAllProductsReducer
});


export default rootReducer;


