import {
    GET_CATEGORY
} from './categories.action.js';

const initialState = {
    fetching: false,
    fetched: false,
    error: null,
    initialized: false,
    categories: [],
};


export default function (state = initialState, action) {
    switch (action.type) {
        case GET_CATEGORY + "_FULFILLED":
            {
                let data = action.payload;
                return {
                    ...state,
                    initialized: true,
                    fetching: false,
                    fetched: true,
                    categories: data && data.Items ? data.Items : [],
                };
            }
    }
    return state;
}




