import React, { Component, PropTypes } from 'react';

export default class ErrorLayout extends Component {
    render() {
        return <div>
            <div className="not-found">
                {this.props.children}
            </div>
            <div className="copyright">
                <p>© 2016 Creative 404. All rights reserved | Design by  <a href="http://w3layouts.com/" target="_blank">  W3layouts </a></p>
            </div>
        </div >
    }
}




