import * as constants from './spinner.action.js';

const initialState = {
    showSpinner: false
};

export default function (state = initialState, action) {
    switch (action.type) {
        case constants.SHOW_SPINNER: { return { ...state, showSpinner: true }; }
        default: { return { ...state, showSpinner: false }; }
    }
    return state;
}




