import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import renderBody from '../../shared/components/page';
import { initializePage } from '../list_all_products/list_all_products.action';
import constants from '../../shared/constants';
import Product from '../../products/product/product.container';
import { showLoading, hideLoading } from 'react-redux-loading-bar';
import Pagination from '../pagination/pagination.container';

class ListAllProducts extends Component {
    constructor(props) {
        super(props);
        this.pageIndex = constants.page_Index;
        this.pageSize = constants.page_Size;
        this.renderListProducts = this.renderListProducts.bind(this);
    }
    componentDidMount() {
        this.props.initializePage(this.pageIndex, this.pageSize,
            this.props.showLoading, this.props.hideLoading).then((data) => {

            });
    }

    renderProductItem(data) {
        return
    }

    renderListProducts(list_products) {
        return list_products.map((product, index) => {
            return (<div key={index}>
                <Product {...product} />
            </div >)
        })
    }

    render() {
        return (
            <div className="women-product">
                <div className=" w_content">
                    <div className="women">
                        <a href="#"><h4>Enthecwear - <span>4449 itemms</span> </h4></a>
                        <ul className="w_nav">
                            <li>Sort : </li>
                            <li><a className="active" href="#">popular</a></li> |
			     	<li><a href="#">new </a></li> |
			     	<li><a href="#">discount</a></li> |
			     	<li><a href="#">price: Low High </a></li>
                            <div className="clearfix"> </div>
                        </ul>
                        <div className="clearfix"> </div>
                    </div>
                </div>
                <div className="grid-product">
                    {this.props.list_products ? this.renderListProducts(this.props.list_products) : <div ></div >}
                    <div className="clearfix"> </div>
                    <Pagination />
                    <div className="clearfix"> </div>
                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        list_products: state.listAllProductsReducer.products
    };
}
function mapDispatchToProps(dispatch) {
    return bindActionCreators({
        initializePage,
        showLoading,
        hideLoading
    }, dispatch);
}
export default connect(mapStateToProps, mapDispatchToProps)(renderBody(ListAllProducts));


