const SHOW_SPINNER = "SHOW_SPINNER"
function showSpinner() {
    return { type: SHOW_SPINNER }
}

const CLOSE_SPINNER = "CLOSE_SPINNER"
function closeSpinner() {
    return { type: CLOSE_SPINNER }
}

export {
    CLOSE_SPINNER,
    SHOW_SPINNER,
    showSpinner,
    closeSpinner
}


