const TYPE_PREFIX = "@@products/list_products/";
export const INITIALIZE_PAGE = TYPE_PREFIX + "INITIALIZE_PAGE";
import FetchHelper from '../../shared/helpers/fetch/fetch_helper';
import constants from '../../shared/constants';

export function initializePage(pageIndex, pageSize, beforeFetch, afterFetch) {
    return (dispatch, getState) => {
        return dispatch({
            type: INITIALIZE_PAGE,
            payload: FetchHelper.fetch(`${constants.api_root}/api/Home/SearchProduct?name=&value=&page=${pageIndex}&size=${pageSize}`, {
                method: 'GET',
            }, beforeFetch, afterFetch)
        })
    }
}





