const TYPE_PREFIX = "@@home/product_item/";
export const GET_PRODUCT_IMAGE = TYPE_PREFIX + "GET_PRODUCT_IMAGE";
import FetchHelper from '../../shared/helpers/fetch/fetch_helper';
import constants from '../../shared/constants';

export function getProductImageItem(id) {
    return (dispatch, getState) => {
        return dispatch({
            type: GET_PRODUCT_IMAGE,
            payload: FetchHelper.fetch(`${constants.api_root}/api/Home/Image?id=${id}`, {
                method: 'GET',
            })
        })
    }
}


