import 'bootstrap/dist/css/bootstrap.css';
//import 'font-awesome/css/font-awesome.min.css';
//import '../assets/css/bootstrap.css';
import '../assets/css/style.css';

import React, { Component, PropTypes } from 'react';
import NavigationBar from '../navigation_bar/navigation.container';
import Categories from '../categories/categories.container';
import Footer from '../footer/footer.container';
import TopHeader from '../top_header/top_header.container';

//Using HOC
const shareLayout = (Header, Navigation, Categories, Footer) => {
    return class extends Component {
        constructor(props) {
            super(props);
        }

        render() {
            return (
                <div>
                    <div className="header">
                        <div className="top-header">
                            {Header ? <Header {...this.props} /> : <div ></div>}
                        </div>
                        <div className="bottom-header">
                            {Navigation ? <Navigation {...this.props} /> : <div></div>}
                        </div>
                    </div>
                    <div className="container">
                        {this.props.children}
                        {Categories ? <Categories {...this.props} /> : <div></div>}
                        <div className="clearfix"> </div>
                    </div>
                    <div className="footer">
                        {Footer ? <Footer {...this.props} /> : <div></div>}
                    </div>
                </div >
            )
        }
    }
}

const Layout = shareLayout(TopHeader, NavigationBar, Categories, Footer);
export default Layout;




