import {
    GET_PRODUCT_IMAGE
} from './product_item.action.js';

const initialState = {
    fetching: false,
    fetched: false,
    error: null,
    initialized: false,

};

export default function (state = initialState, action) {
    switch (action.type) {
        case GET_PRODUCT_IMAGE + "_FULFILLED":
            {
                let data = action.payload;
                return {
                    ...state,
                    initialized: true,
                    fetching: false,
                    fetched: true,
                };
            }
    }
    return state;
}




