import React, { Component } from 'react';
import Spinner from '../../../shared/spinner/spinner.container';
const styles = {
    spinner: { position: 'relative', height: '200px' }
}

const showProduct = (ProductThumb, ProductInforSummary) => {
    return class extends Component {
        render() {
            return (
                <div className="product-grid">
                    <div className="content_box">
                        <div className="left-grid-view grid-view-left">
                            <ProductThumb />
                        </div>
                        <ProductInforSummary {...this.props} />
                    </div>
                </div>
            )
        }
    }
}

const ProductInforSummaryView = (product) => {
    return (
        <div>
            <h4><a href="#">{product.ProductName} </a></h4>
            <p>{product.Description}</p>
            {product.ProductCode}
        </div>
    )
}

const ProductImage = () => {
    return (
        <div>
            <div style={styles.spinner}>
                <Spinner />
            </div>

            {/*status || imagesrc || 1 ?
            <Spinner />
            :  <a href="#">
                <img className="img-responsive chain"
                    src={require(imagesrc)} alt=" " />
            </a>*/}
        </div>
    )
}

const ProductView = showProduct(ProductImage, ProductInforSummaryView);
export default ProductView



