
class FetchHelper {
    constructor() {
        let access_alias = getSubDomainInUrl();
        this.defaultHeaders = {
            'Content-Type': 'application/json',
            'access_alias': access_alias,
        }
    }

    fetch(input, init = {}, beforeFetch, afterFetch) {
        let initWithDefaultHeaders = {
            ...init,
            headers: mergeWithDefaultHeaders(init.headers, this.defaultHeaders)
        }
        if (beforeFetch)
            beforeFetch();
        return new Promise((resolve, reject) => {
            fetch.apply(null, [input, initWithDefaultHeaders])
                .then(response => {
                    if (afterFetch)
                        afterFetch();
                    resolve(response.json())
                }).catch(error => {
                    if (afterFetch)
                        afterFetch();
                    reject(error)
                })
        })
    }
}

/*** PRIVATE METHODS: ***/

function getSubDomainInUrl() {
    let url = window.location.host;
    var subdomain = '';
    if (url) {
        let parts = url.split('.');
        subdomain = parts && parts.length >= 2 ? parts[0] : '';
    }
    return subdomain;
}

function mergeWithDefaultHeaders(headers = {}, defaultHeaders) {
    var headerObj = {}
    if (headers instanceof Headers) {
        for (let [key, value] of headers) {
            headerObj[key] = value
        }
    } else {
        headerObj = headers
    }
    return Object.assign({}, defaultHeaders, headers);
}

const fetchHelperInstance = new FetchHelper();

export default fetchHelperInstance



