import React, { Component } from 'react';
import {
    Router, Route, Redirect, IndexRedirect,
    IndexRoute, hashHistory, browserHistory
} from 'react-router';
import App from '../modules/shared/components/app/app';
import Layout from '../modules/shared/layouts/layout';
import ErrorLayout from '../modules/shared/layouts/error_layout';
import Error404 from '../modules/shared/404/404';
import ListProductsPage from '../modules/home/list_products/list_products.container';
import ListAllProductPage from '../modules/products/list_all_products/list_all_products.container.js';

export default class RouteConfig extends Component {
    render() {
        return (
            <Router history={browserHistory}>
                <Route path={"#"} component={App}>
                    <Route path={"/"} component={Layout}>
                        <IndexRoute component={ListProductsPage} />
                        <Route path={"/list-products"} component={ListAllProductPage}/>
                    </Route>
                </Route >
                <Route path={"/*"} component={ErrorLayout}>
                    <IndexRoute component={Error404} />
                </Route>
            </Router>
        );
    }
}





