const TYPE_PREFIX = "@@categories/category/";
export const GET_CATEGORY = TYPE_PREFIX + "GET_CATEGORY";
import FetchHelper from '../../shared/helpers/fetch/fetch_helper';
import constants from '../../shared/constants';

export function getCategory(pageIndex, pageSize) {
    return (dispatch, getState) => {
        return dispatch({
            type: GET_CATEGORY,
            payload: FetchHelper.fetch(`${constants.api_root}/api/Home/SearchCategory?name=&value=&page=${pageIndex}&size=${pageSize}`, {
                method: 'GET'
            })
        })
    }
}


